<style scoped lang="scss">

</style>

<template>
  <div>
    <el-breadcrumb separator="/">
      <el-breadcrumb-item><i class="el-icon-date"></i> 案例</el-breadcrumb-item>
      <el-breadcrumb-item>基础表格</el-breadcrumb-item>
    </el-breadcrumb>

    <div class="search mt-20 mb-20">
      <el-button type="primary" icon="delete" @click="deleteAll(1)">批量删除</el-button>
      <el-select v-model="selectValue" filterable placeholder="请选择">
        <el-option-group filterable
          v-for="item in selectList"
          :key="item.title"
          :label="item.title"
          :value="item.title">
          <el-option
            v-for="el in item.options"
            :key="el.key"
            :label="el.value"
            :value="el.key">
          </el-option>
        </el-option-group>
      </el-select>
    </div>

    <el-table :default-sort = "{prop: 'date', order: 'descending'}"
      ref="multipleTable" :data="tableList" border @selection-change="fnTableSelected">
      <el-table-column label="选择">
        <el-table-column type="selection" width="55"></el-table-column></el-table-column>

        <el-table-column label="信息">
          <el-table-column prop="date" label="日期" sortable width="120"></el-table-column>
          <el-table-column prop="name" label="姓名"></el-table-column>
          <el-table-column prop="age" sortable label="年龄"></el-table-column>
          <el-table-column prop="address" label="地址" :min-width="150" show-overflow-tooltip></el-table-column>
          <el-table-column label="操作" width="160">
            <template scope="scope">
              <el-button size="mini" type="primary"
                @click.native.prevent="edit(scope.$index, tableData4)">编辑
              </el-button>
              <el-button size="mini" type="danger"
                @click="handleDelete(scope.$index, scope.row)">删除
              </el-button>
            </template>
          </el-table-column>
      </el-table-column>
    </el-table>

    <el-dialog title="提示" :visible.sync="dialogVisible" width="30%">
      <span>确定删除全部?</span>
      <span slot="footer" class="dialog-footer">
        <el-button @click="dialogVisible = false">取 消</el-button>
        <el-button type="primary" @click="deleteAll(2)">确 定</el-button>
      </span>
    </el-dialog>
  </div>
</template>

<script type="text/babel">
	export default {
    data() {
      return {
        selectValue: '',
        selectList: [
          {
            title: '热门城市',
            options: [
              { key: '北京', value: '北京'},
              { key: '上海', value: '上海' },
              { key: '广州', value: '广州' },
              { key: '深圳', value: '深圳' }
            ]
          },
          {
            title: '城市名',
            options: [
              { key: '重庆', value: '重庆'},
              { key: '赣州', value: '赣州' },
              { key: '武汉', value: '武汉' }
            ]
          }
        ],

        tableSelected: [],

        tableData: [
          {
            date: '2016-05-02',
            name: '王小虎',
            age: 24,
            address: '上海市普陀区金沙江路 1518 弄'
          },
          {
            date: '2016-05-02',
            name: '张三',
            age: 45,
            address: '广东省 广州市 榕城区'
          },
          {
            date: '2016-05-03',
            name: '李四',
            age: 39,
            address: '广东省 广州市 榕城区'
          },
          {
            date: '2016-05-04',
            name: '王五',
            age: 24,
            address: '湖北 武汉 江岸区'
          },
          {
            date: '2016-05-04',
            name: '李明',
            age: 24,
            address: '湖北 武汉 江岸区'
          }
        ],
        dialogVisible: false,
        selected: []
      }
    },

    computed: {
      tableList() {
        const that = this
        return this.tableData.filter(function(val) {
          if(val.address.indexOf(that.selectValue) > -1) {
            return val
          }
        })
      }
    },

    mounted() {

    },

    methods: {
      fnTableSelected(n) {
        this.tableSelected = n
      },
      deleteAll(n) {
        if (this.tableSelected.length == this.tableList.length && this.tableList.length != 1) {
          if (n == 1) {
            this.dialogVisible = true
          } else if (n == 2) {
            this.dialogVisible = false
            this.tableData = []
            this.tableSelected = []
            this.$message.success("删除成功")
          }
        } else {
          const that = this
          for (let i = 0, len = that.tableSelected.length; i < len; i++) {
            that.tableData.forEach(function(val, index) {
              if (that.tableSelected[i].name == val.name && that.tableSelected[i].address == val.address) {
                that.tableData.splice(index, 1)
                that.$message.success("删除成功")
              }
            })
          }
        }
      },
      edit() {

      },
      handleDelete(index, row) {
        this.tableData.forEach((val, index) => {
          if (row.name == val.name && row.address == val.address) {
            this.tableData.splice(index, 1)
          }
        })
        this.$message.success("删除"+row.name+"成功")
      }
    }
  }
</script>
